@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&family=Open+Sans&display=swap);
* {
	margin: 0;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* Below are the general styles - linking different fonts to their elements. */

body,
ul,
h1,
h2,
h3 p {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3 {
	font-family: "Nunito Sans", sans-serif;
	font-weight: 100;
}

body {
	font-family: "Inter", sans-serif;
	background-color: #eef0f1;
	font-weight: 100;
}

.wrapper {
	max-width: 1100px;
	width: 100%;
	margin: 0 auto;
}

/* Below are styles for the nav*/

.topnavcontainer {
	margin-bottom: -0.3rem;
}

.topnav img {
	height: 2.5rem;
	padding: 1rem;
	border-radius: 10px;
}

.topnav {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.topnav ul {
	list-style: none;
	display: flex;
	padding: 1rem;
}

.topnav .desk a {
	border-bottom: 1px solid black;
	padding: 0.3rem;
}

.topnav .desk a {
	text-decoration: none;
	font-size: 1.1rem;
	color: black;
	margin: 1rem;
}

.topnav ul a:hover {
	background-color: black;
	color: white;
}

/* Below are the Styles for the header*/

.head {
	min-width: 100%;
	background-color: #040d21;
	background-size: cover;
}

.head .headertext {
	color: #9ef5d1;
	letter-spacing: 0.1rem;
	text-align: center;
	padding: 7rem;
}

.headertext h1 {
	font-size: 2.5rem;
	margin-bottom: 2rem;
}

.headertext h2 {
	font-size: 1.9rem;
	color: white;
	word-break: break-word;
}

/* media queries */

/* media */

@media (max-width: 1024px) {
	.topnav {
		justify-content: space-between;
	}
}

@media (max-width: 834px) {
	.topnav ul a:hover {
		background-color: transparent;
	}

	.topnav img {
		margin-left: 1rem;
	}

	.topnav {
		display: flex;
		justify-content: space-between;
	}
}

@media (max-width: 600px) {
	.head .headertext {
		padding: 5rem;
	}

	.head h1 {
		font-size: 2rem;
	}

	.head h2 {
		font-size: 1.35rem;
	}
}

@media (max-width: 500px) {
	.head .headertext {
		padding: 3rem;
	}
}

.blog_topics {
	padding: 3rem;
	font-weight: 100;
	letter-spacing: 0.1rem;
}

.blog_topics .flex-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.blog_topics .container {
	/* -webkit-box-shadow: 0px 0px 10px lightgrey;
	box-shadow: 0px 0px 10px lightgrey; */
	width: calc((87% / 3) - 3%);
	margin: 1%;
	background-color: #8193b2;
	border-radius: 10px;
	transition: transform 1s;
	cursor: pointer;
}

.blog_topics .container img {
	padding-top: 3rem;
	width: 15rem;
	height: 10rem;
	display: block;
	margin: auto;
	-webkit-filter: hue-rotate(50deg);
	        filter: hue-rotate(50deg);
}

.blog_topics .container h2 {
	text-align: center;
	font-size: 1.5rem;
	font-weight: 100;
	margin-top: 2rem;
	padding: 0.5rem;
	background-color: #040d21;
	color: white;
}

.blog_topics .postnumbers {
	font-size: 1.1rem;
	color: lightgreen;
}
.blog_topics .container p {
	font-size: 1.2rem;
	line-height: 30px;
	text-align: center;
	padding: 0.5rem;
	color: black;
}

/* media */

@media (max-width: 1024px) {
	.blog_topics .container {
		width: calc((100% / 2) - 2%);
	}
}

@media (max-width: 600px) {
	.blog_topics {
		margin: 0.5rem;
		padding: 0rem;
		margin-top: 2rem;
	}
	.blog_topics .flex-container {
		display: block;
		margin: auto;
		margin-right: 5rem;
		margin-left: 5rem;
	}
	.blog_topics .container {
		width: calc((100% / 1) - 2%);
		margin-bottom: 2rem;
	}

	.blog_topics .container p {
		padding-bottom: 3rem;
		font-size: 1.1rem;
	}
}

@media (max-width: 500px) {
	.blog_topics .flex-container {
		margin-right: 2rem;
		margin-left: 2rem;
	}
}

@media (max-width: 384px) {
	.blog_topics .flex-container {
		margin-right: 1rem;
		margin-left: 1rem;
	}

	.blog_topics .container p {
		padding-left: 1rem;
		padding-right: 1rem;
	}
}

/* blog general styles */

.blogs {
	margin: 3rem;
}

.blog {
	margin: 3rem;
	padding-bottom: 1.5rem;
}

.titletag {
	display: flex;
	margin-left: 2rem;
	margin-right: 2rem;
	padding-top: 1rem;
	padding-bottom: 1rem;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid black;
}

.title {
	font-weight: 500;
	font-size: 2rem;
}

.hashtag {
	font-size: 1.2rem;
	font-weight: 500;
}

/* actual blog post  */

.blogbody {
	padding: 2rem;
	padding-top: 1.5rem;
}

.blog h2,
.blog h3,
.blog p,
.blog li {
	line-height: 2;
}

.blog h2 {
	font-size: 1.7rem;
	font-weight: 500;
}

.blog h3 {
	font-size: 1.7rem;
	font-weight: 500;
}

.blog p {
	font-size: 1.3rem;
}

.blog li {
	font-size: 1.2rem;
}

.blogbody .focus {
	color: darkred;
}

.blogbody .focusdifferent {
	color: darkred;
}

.blogbody ul {
	margin-left: 4rem;
	list-style: circle;
}

.blogbody ul li {
	margin-bottom: 2rem;
	word-break: break-word;
}

.blogbody ol li {
	margin-bottom: 2rem;
}

.blogbody .code {
	background-color: black;
	color: white;
	padding: 1rem;
	list-style: none;
	width: 65%;
}

.paragraph {
	/* this applies only for blogs where i wrap elements in div that has this class */
	padding-top: 1rem;
}

.paragraph h2 {
	/* this applies only for blogs where i wrap elements in div that has this class */
	padding-bottom: 2rem;
}

.noncodeimg {
	border-radius: 10px;
	width: 80%;
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
	margin-top: 2rem;
	margin-bottom: 2rem;
}

/* end of blog animation  */

.boite {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	align-content: center;
	height: 50px;
	background-color: #040d21;
	border-radius: 4px;
}

.cercle3 {
	position: absolute;
	padding: 0px;
	width: 45px;
	height: 45px;
	border: 3px solid #9ef5d1;
	border-radius: 50%;
	background-color: transparent;

	box-sizing: border-box;
	-ms-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;

	transform-style: preserve-3d;
	-webkit-transform-style: preserve-3d;
	-moz-transform-style: preserve-3d;
	-ms-transform-style: preserve-3d;
	animation: rotateCercle3 1s linear infinite;
}

.cercle3:nth-child(2) {
	animation-delay: 0.2s;
}

.cercle3:nth-child(3) {
	animation-delay: 0.5s;
}

.cercle3:nth-child(4) {
	animation-delay: 0.7s;
}

/* media */

@media (max-width: 1024px) {
	.blog .titletag {
		flex-direction: column;
		align-items: unset;
	}

	.hashtag {
		margin-top: 1rem;
	}
}

@media (max-width: 600px) {
	.blogs {
		padding-top: 0rem;
		margin: 0rem;
	}
}

@media (max-width: 500px) {
	/* .blog h2 {
		font-size: 1.9rem;
	}

	.blog h3 {
		font-size: 1.8rem;
	}

	.blog p {
		font-size: 1.7rem;
	}

	.blog li {
		font-size: 1.6rem;
	} */
}

/* animation for end of blog */

@keyframes rotateCercle3 {
	0% {
		transform: rotateX(45deg) rotateY(0deg);
	}
	50% {
		transform: rotateX(45deg) rotateY(90deg);
	}
	100% {
		transform: rotateX(45deg) rotateY(180deg);
	}
}

